import './globals.css';
import Header from '@/components/Header';
import NeuralScene from './three/NeuralCore';

export default function RootLayout({
  children,
}: {
  children: React.ReactNode
}) {
  return (
    <html lang="en">
      <body>
        {/* Header - vast bovenaan */}
        <Header />

        {/* NeuralScene achtergrond */}
        <div className="neural-scene-container">
          <NeuralScene />
        </div>

        {/* Hoofdcontent */}
        <main className="relative z-0">
          {children}
        </main>
      </body>
    </html>
  )
}